@font-face {
	font-family: "ChelseaMarket";
	src: url("../font/ChelseaMarket-Regular.ttf") format("truetype");
}
.ChelseaMarket-font{
	font-family: "ChelseaMarket";  
} 

body{
	margin: 0 auto;
	background-image: url(../img/Fundo.JPG);
	background-position: center;
    background-attachment: fixed;
	font-family: "ChelseaMarket";
	width: 100%;
}

#cab img{	
	height: 85px;
}

#cab h1 p{
	color: fuchsia;
	text-align: center;
	font-size: 24px;
}
#menu{
	float: left;
	width: 100%;
	position: fixed;
	box-shadow: 0px 0px 50px #b47eab;
	top: 0;
	z-index: 10;
}
#menu a:hover {
	color: fuchsia;
}
#menu ul {
	text-align: center;
	list-style: none;
	background: linear-gradient(to bottom, #f7d2f7, #ffffff);
	padding: 0px;
	margin:0px;
}
#menu li {
	height: 45px;
	display:inline-table;
	text-align: center;
	vertical-align: middle;
	/* width: 200px; */
    margin-right: 3%;
	font-weight: bolder;
	/*border-right: 1px solid #DDD;*/
}
#menu ul li a {
	text-decoration: none;
	font-size: 20px;
	color: #FF69B4;
}

@media (max-width: 600px) {
	.show-big{
		display: none !important;
	}
}

@media (min-width: 600px) {
	.hide-big{
		display: none !important;
	}
}

@media (max-width: 800px) {
	#cab img{	
		height: 60px;
	}

	#menu ul li a {		
		font-size: 16px !important;		
	}
}

.menu-lateral{
	margin: 20px;
}

.menu-lateral div {
	font-size: 24px;
    margin-bottom: 13%;    
    text-shadow: 10px 10px 11px rgb(255, 127, 172);
}

.menu-lateral a {
	text-decoration: none;
	font-size: 24px;
    color: #FF69B4;
    font-family: "ChelseaMarket";
    margin-left: 12px;
}

.menu-laterial-icon{
	color: #FF69B4;
}


