div.thumb {
	width: 145px;
    height: 235px;
    float: left;
    margin: 0 14px 14px 0;
    padding: 0;
    border-radius: 10px;
    background-color: rgba(248, 214, 248, 0.65);
    box-shadow: 5px 5px 18px #554e4e;
}
div.thumb a {
	float:left;
	padding:13px;
	text-decoration: none;
    text-align: center;
    color: #797479;
    font-size: 11px;
}
div.thumb a:hover {
	background:#da70d6;
	border-radius: 10px;
	height: 209px;
}
div.thumb img {
	width:120px;
	height:120px;
	border-radius: 10px;
}

